

























@import '../assets/design/index.less';
    .moviecard {
        padding: 10px;
        margin: 10px 0;
        border-radius: 4px;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        box-shadow: @defaultBoxShadow #ddd;
        background-color: #fff;

        .Image {
            width: 80px;
            border-radius: 6px;
            overflow: hidden;

            img { width: 100%; }
        }

        .Content {
            padding: 10px 0;
            margin-left: 10px;
            .title {
                span { display: block; }
            }
        }
    }

@primary-color: #1b1b1b;@item-hover-bg: #ddd;@border-radius-base: 2px;@input-hover-border-color: #0d499e;